body {
  color: #5e6282;
  background-color: #fff;
  font-family: Poppins, sans-serif;
  font-size: 16px;
  font-weight: 400;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.img--no-gap {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  display: block;
}

button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-style: none;
  padding: 0;
}

.container {
  max-width: 1440px;
