body {
  background-color: #fff;
  font-family: Poppins, sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #5e6282;
}

h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.img--no-gap {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  padding: 0;
  border-style: none;
  background-color: transparent;
  cursor: pointer;
}

.container {
  outline: 1px solid red;
  max-width: 1440px;
  padding-left: 25px;
  padding-right: 10px;
  font-size: 16px;
  margin-left: auto;
  margin-right: auto;
}
@media screen and (min-width: 480px) and (max-width: 767px) {
