  top: 0;
  left: -130px;
  width: 500px;
  height: 500px;
}
@media screen and (min-width: 1440px) {
  .hero__img {
    position: absolute;
    top: -112px;
    left: -100px;
    width: 784px;
    height: 764px;
  }
}

.hero__icon-plane {
  position: absolute;
}
.hero__icon-plane:nth-of-type(1) {
  top: -100px;
  left: -100px;
}
@media screen and (min-width: 1440px) {
  .hero__icon-plane:nth-of-type(1) {
    top: -50px;
    left: -50px;
  }
}
@media screen and (min-width: 1440px) {
  .hero__icon-plane:nth-of-type(1):hover {
    -webkit-animation-name: movePlane;
            animation-name: movePlane;
    -webkit-animation-duration: 3000ms;
            animation-duration: 3000ms;
  }
}
.hero__icon-plane:nth-of-type(2) {
  display: none;
}
@media screen and (min-width: 1024px) {
  .hero__icon-plane:nth-of-type(2) {
    display: block;
    top: 0px;
    right: -100px;
    z-index: -1;
  }
}

@-webkit-keyframes movePlane {
  0% {
    top: -50px;
    left: -50px;
  }
  40% {
    top: -50px;
    left: -35px;
  }
  100% {
    top: -350px;
    left: 400px;
  }
}

@keyframes movePlane {
  0% {
    top: -50px;
    left: -50px;
  }
  40% {
    top: -50px;
    left: -35px;
  }
  100% {
    top: -350px;
    left: 400px;
  }
}
